import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';
import Image from 'gatsby-image';
import { Card, CardContent, Box, Typography, Grid, Button, makeStyles, Theme } from '@material-ui/core';
import PostCount from 'components/Utils/PostCount';
import CategoryCount from 'components/Utils/CategoryCount';
import TagCount from 'components/Utils/TagCount';
import * as Icon from 'react-feather';

const useStyles = makeStyles<Theme>(theme => ({
  contents: {
    textAlign: 'center',
  },
  profileImage: {
    minWidth: 50,
    marginBottom: theme.spacing(2),
  },
  introduce: {
    marginBottom: theme.spacing(2),
  },
  blogData: {
    marginBottom: theme.spacing(2),
    '& dt': {
      color: theme.palette.grey['600'],
      fontSize: theme.typography.caption.fontSize,
      marginBottom: theme.spacing(0.1),
    },
    '& dd': {
      fontSize: theme.typography.h5.fontSize,
      fontWeight: 300,
      margin: 0,
    },
  },
  emailButtonWrapper: {
    marginBottom: theme.spacing(2),
  },
  emailIcon: {
    width: '1rem',
    marginRight: theme.spacing(1),
  },
  emailButton: {
    width: '100%',
    color: theme.palette.getContrastText(theme.palette.grey['800']),
    backgroundColor: theme.palette.grey['800'],
    '&:hover': {
      backgroundColor: theme.palette.grey['900'],
    },
  },
  externalLinkbuton: {
    width: '100%',
  },
}));

const Bio = () => {
  const styles = useStyles();
  const data = useStaticQuery(graphql`
    query {
      avatar: file(absolutePath: { regex: "/me.jpg/" }) {
        childImageSharp {
          fixed(width: 128, height: 128, cropFocus: CENTER) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      site {
        siteMetadata {
          author {
            name
            summary
            email
          }
          social {
            github
            linkedin
          }
        }
      }
    }
  `);

  const {
    avatar: {
      childImageSharp: { fixed: avatar },
    },
    site: {
      siteMetadata: { author, social, email },
    },
  } = data;

  return (
    <Card>
      <CardContent className={styles.contents}>
        <Image
          fixed={avatar}
          alt={author.name}
          imgStyle={{
            borderRadius: `50%`,
          }}
          className={styles.profileImage}
        />
        <Box className={styles.introduce}>
          <Typography variant="h6" component="h6">
            {author.name}
          </Typography>
          <Typography color="textSecondary" variant="subtitle2">
            {author.summary}
          </Typography>
        </Box>
        <Grid container>
          <Grid item xs={4} className={styles.blogData}>
            <dt>포스트</dt>
            <dd>
              <PostCount />
            </dd>
          </Grid>
          <Grid item xs={4} className={styles.blogData}>
            <dt>카테고리</dt>
            <dd>
              <CategoryCount />
            </dd>
          </Grid>
          <Grid item xs={4} className={styles.blogData}>
            <dt>태그</dt>
            <dd>
              <TagCount />
            </dd>
          </Grid>
        </Grid>
        <Box className={styles.emailButtonWrapper}>
          <Button variant="contained" href={`mailto:${email}`} className={styles.emailButton}>
            <Icon.Mail className={styles.emailIcon} />
            이메일
          </Button>
        </Box>
        <Grid container>
          <Grid item xs={4}>
            <Button href={social.github} target="_blank" className={styles.externalLinkbuton}>
              <Icon.GitHub />
            </Button>
          </Grid>
          <Grid item xs={4}>
            <Button href={social.linkedin} target="_blank" className={styles.externalLinkbuton}>
              <Icon.Linkedin />
            </Button>
          </Grid>
          <Grid item xs={4}>
            <Button href={social.rss} target="_blank" className={styles.externalLinkbuton}>
              <Icon.Rss />
            </Button>
          </Grid>
        </Grid>
      </CardContent>
    </Card>
  );
};

export default Bio;
