import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';

const TagCount = () => {
  const {
    allMarkdownRemark: { group },
  } = useStaticQuery(graphql`
    query {
      allMarkdownRemark {
        group(field: frontmatter___categories) {
          totalCount
        }
      }
    }
  `);
  const count = group.length;

  return <>{count}</>;
};

export default TagCount;
