import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';

const TagCount = () => {
  const {
    allMarkdownRemark: { totalCount: count },
  } = useStaticQuery(graphql`
    query {
      allMarkdownRemark {
        totalCount
      }
    }
  `);

  return <>{count}</>;
};

export default TagCount;
